}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

body {
  background-image: url(/src/img/prestige-COD.gif);
  background-repeat: repeat;
  color: #FFFFFF;
  transition: all 0.3s ease-in-out;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #990606;
}

.bg-blanco {
  background-color: #FFFFFF;
}

.margen-menos-10 {
  margin-top: -10rem;
}

.pad-bot-0 {
  padding-bottom: 0 !important;
}

.h2-sombra1 h2 {
  font-size: 3.6rem;
  display: inline-block;
}
.h2-sombra1 h2::after {
  content: "";
  background-color: #990606;
  height: 1rem;
  width: 105%;
  display: block;
  margin: -1.4rem 0 0 0;
}

.h2-sombra2 h2 {
  font-size: 3.6rem;
  display: inline-block;
}
.h2-sombra2 h2::after {
  content: "";
  background-color: #FFFFFF;
