  width: 105%;
  display: block;
  margin: -1.4rem 0 0 0;
}

.degradado {
  color: transparent;
  background: linear-gradient(to right, #990606 0%, #e2b521 100%);
  -webkit-background-clip: text;
  background-clip: text;
}

.sombra1 {
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.3);
}

.titulo-principal {
  text-align: center;
  font-family: "Carter One", cursive;
}
.titulo-principal h2 {
  position: relative;
  text-transform: uppercase;
  font-size: 4rem;
  font-weight: 700;
}
.titulo-principal h2 span {
  color: #e2b521;
}
.titulo-principal h2 .bg-texto {
  position: absolute;
  top: 50%;
  left: 50%;
  color: #535353;
  transition: all 0.3s ease-in-out;
  z-index: -1;
  transform: translate(-50%, -75%);
  font-weight: 800;
  font-size: 5rem;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

p {
  color: #000000;
  font-size: 2.1rem;
  font-family: "Overlock", cursive;
}

a {
  display: inline-block;
  color: #FFFFFF;
  font-family: "Overlock", cursive;
  font-size: 2rem;
}

/* Añade a este archivo los demas archivos de esta carpeta con @forward y recuerda añadadirlos al archivo principal (app.scss) */
section {
  min-height: 100vh;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  padding: 3rem 2rem;
}
@media (min-width: 768px) {
  section {
    padding: 3rem 18rem;
  }
}

.seccion {
  transform: translateY(-100%) scale(0);
  transition: all 0.3s ease-in-out;
}

.sec-1 {
  display: none;
  transform: translateY(0) scale(1);
}

.sec-2 {
  display: none;
  transform: translateY(0) scale(1);
}

.sec-3 {
  display: none;
  transform: translateY(0) scale(1);
}

.sec-4 {
  display: none;
  transform: translateY(0) scale(1);
}

.sec-5 {
  display: none;
  transform: translateY(0) scale(1);
}

.activa {
  display: block;
  animation: scaleAnim 1s ease-in-out;
}
@keyframes scaleAnim {
  0% {
    transform: translateY(-100%) scaleY(0);
  }
  100% {
    transform: translateY(0) scaleY(1);
  }
}

header {
  height: 100vh;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  header {
    overflow: hidden;
  }
}

.header-grid {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .header-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    height: 100%;
  }
}
.header-grid .header_imagen {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.header-grid .header_imagen .imagen {
  height: 500px;
  width: 100%;
  background-color: #000000;
}
@media (min-width: 768px) {
  .header-grid .header_imagen .imagen {
    border-radius: 15px;
    height: 90vh;
    width: 68%;
    border: 1.5rem solid white;
    background-color: #000000;
    transition: all 0.3s ease-in-out;
  }
}
.header-grid .header_imagen .imagen img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.3s ease-in-out;
  filter: grayscale(100%);
}
.header-grid .header_imagen .imagen img:hover {
  filter: grayscale(0%);
}
@media (min-width: 768px) {
  .header-grid .header_imagen .imagen:hover {
    transform: rotate(-4deg) scale(1.1);
  }
}
.header-grid .header-heading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2rem;
}
@media (min-width: 768px) {
  .header-grid .header-heading {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0;
    padding-right: 18rem;
  }
}
.header-grid .header-heading .nombre {
  position: absolute;
  top: 29rem;
  margin-bottom: 0;
  font-size: 4rem;
  font-family: "Carter One", cursive;
  color: #e2b521;
}
.header-grid .header-heading .nombre span {
  color: #990606;
}
@media (min-width: 768px) {
  .header-grid .header-heading .nombre {
    position: unset;
  }
}
.header-grid .header-heading p {
  margin: 1.5rem 0;
  line-height: 2rem;
  color: #FFFFFF;
  font-size: 2.1rem;
  font-family: "Overlock", cursive;
  font-weight: 700;
}
.header-grid .header-heading .btn-principal {
  margin-bottom: 10rem;
  border-radius: 30px;
  color: inherit;
  font-weight: 600;
  position: relative;
  border: 1px solid #e2b521;
  display: flex;
  align-self: flex-start;
  align-items: center;
  overflow: hidden;
  font-family: "Overlock", cursive;
  text-decoration: none;
}
.header-grid .header-heading .btn-principal .btn-texto {
  padding: 0 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 1rem;
  margin-right: -11px;
}
.header-grid .header-heading .btn-principal .btn-texto::after {
  content: "";
  background-color: #e2b521;
  display: flex;
  border-radius: 50%;
  padding: 1.7rem;
}
.header-grid .header-heading .btn-principal .btn-texto::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  transform: translateX(100%);
  transition: all 0.3s ease-in-out;
  z-index: -1;
}
.header-grid .header-heading .btn-principal .btn-texto:hover {
  color: #e2b521;
}
.header-grid .header-heading .btn-principal .btn-texto:hover::before {
  width: 100%;
  height: 100%;
  background-color: #990606;
  transform: translateX(0);
}
.header-grid .header-heading .btn-principal span::after {
  content: "";
  display: inline-block;
  height: 2rem;
  width: 2rem;
  background-image: url(/build/img/iconos/download.svg);
  margin-left: 1rem;
}

.navegacion {
  width: 100%;
