  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .navegacion {
    height: 50vh;
    width: unset;
    position: fixed;
    z-index: 10;
    top: 0;
    right: 3%;
    bottom: unset;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transform: translateY(50%);
