.cubo {
  position: relative;
  left: 15%;
  top: 25%;
  z-index: -1;
  margin-top: 8rem;
  width: 20rem;
  height: 20rem;
  transform-style: preserve-3d;
  animation: animar 10s linear infinite;
}
@media (min-width: 768px) {
  .cubo {
    left: 25%;
    top: 25%;
    margin-top: unset;
  }
}
@keyframes animar {
  0% {
    transform: rotateX(-30deg) rotateY(0deg);
  }
  100% {
    transform: rotateX(-30deg) rotateY(360deg);
  }
}
.cubo .cara-superior {
  position: absolute;
  top: 0;
  left: 0;
  width: 20rem;
  height: 20rem;
  background: #252525;
  transform: rotateX(90deg) translateZ(10rem);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.cubo .cara-superior .titulo {
  color: #FFFFFF;
  margin: 0;
}
.cubo .caras {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
}
.cubo .caras span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, #252525 0%, #990606 90%);
  transform: rotateY(calc(90deg * var(--lado))) translateZ(10rem);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.cubo .caras span a {
  color: #FFFFFF;
  font-size: 2.8rem;
  font-weight: bold;
  text-transform: uppercase;
  margin: 0;
}
.cubo .sombra {
  position: absolute;
  top: 0;
  left: 0;
  width: 20rem;
  height: 20rem;
  background: #990606;
  transform: rotateX(90deg) translateZ(-25rem);
  filter: blur(2rem);
  box-shadow: 0 0 10rem rgba(153, 6, 6, 0.2), 0 0 20rem rgba(153, 6, 6, 0.4), 0 0 30rem rgba(153, 6, 6, 0.6), 0 0 40rem rgba(153, 6, 6, 0.8), 0 0 50rem #990606;
}

